// 引入vue
import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './App';
import vheader from 'component/mui/Header.vue';

Vue.use(VueRouter);
var router = new VueRouter();

// head所用参数
var hdstyle = {
    back: {
        display: true
    },
    title: {
        display: true
    },
    searchBar: {
        display: 'none',
        placeholder: '请输入商品名称的关键字',
        modelName: 'buyer_activity', // 搜索历史在本地缓存中存放的key
        gotoPage: '../../buyer/activity/search.html', // 进入搜索页点击搜索如果不是返回当前页而是跳转到其他页面，设置跳转页面的url
        searchTypeBut: [{ // 搜索页是否显示搜索类型下拉菜单按钮，不显示不要设置这个值
            name: '商品',
            value: 'goods'
        }, {
            name: '店铺',
            value: 'store'
        }, {
            name: '品牌',
            value: 'bland'
        }],
        hotSearch: false, // 搜索页是否显示热门搜索
        extras: {
        	type: 2,	//活动类型(1:店铺内，2:平台)
        	activity_id: 0
        }
    },
    rightSearchBtn: {
        display: true,
        modelName: 'buyer_activity'
    }
};

// 头部head start
var HeaderVue = Vue.extend(vheader);
var header = new HeaderVue({
    propsData: {
        hdstyle: hdstyle,
        title: '',
        showHeader: true,
        searchValue: ''
    },
    el: 'vheader',
    watch: {
        searchValue: function() {
            if (router.app.curValue === 1 && router.app.sortArrow === 2) {
                router.app.doSearch();
            } else {
                router.app.setSortDefault = true;
            }
        }
    }
});
// 头部head end

mui.init({
    swipeBack: true
});

mui.plusReady(function() {
    header.title = plus.webview.currentWebview().activityInfo.column_name;
    header.hdstyle.searchBar.extras.activity_id = plus.webview.currentWebview().activityInfo.column_code;
    router.start(app, '#app');
});
